import { HTTP } from '@/helpers/httpCommon';
import { getCookie } from '@/helpers/cookies';
import store from '@/plugins/store';
import router from '@/router';

export default function role({ next }) {
	const myRole = store.getters.getMyRole;

	if(!myRole) {
		HTTP.get('user/get/info')
			.then((response) => {
				const myRole = response.data.user_role;
				store.commit('setMyRole', myRole);
				this.isLoading = false;
				return next();
			})
			.catch((error) => {
				if(error.code === 'ERR_NETWORK') {
					const token = getCookie('_token');
					if(!token) router.push('/login');
					else router.push({ 
						name: 'error',
						params: { 
							code: 500
						}
					});
				}
			});
	} else {
		return next();
	}
}