/* eslint-disable no-unused-vars */
import Vue from 'vue';
import VueRouter from 'vue-router';
import store from '../plugins/store';

import role from '@/middleware/role';

const { push } = VueRouter.prototype;

const { isNavigationFailure, NavigationFailureType } = VueRouter;

VueRouter.prototype.push = function (location) {
	return push.call(this, location).catch(error => {
		if (!isNavigationFailure(error, NavigationFailureType.duplicated)) {
			throw Error(error);
		}
	});
};

Vue.use(VueRouter);

const routes = [
	{
		path: '*',
		component: () => import(/* webpackChunkName: "404" */ '@/views/NotFoundView.vue'),
	},
	{
		path: '/',
		name: 'Home',
		meta: { 
			requiresAuth: true,
			middleware: role
		},
		beforeEnter(to, from, next) {
			const myRole = store.getters.getMyRole;
			if(myRole === 'admin') {
				if (to.path !== '/clients') {
					next('/clients');
				} else {
					next();
				}
			} else if (myRole === 'user') {
				if (to.path !== '/templates') {
					next('/templates');
				} else {
					next();
				}
			}
		},
		component: () => import(/* webpackChunkName: "home" */ '../views/HomeView.vue'),
		children: [
			{
				path: 'clients',
				name: 'clients',
				props: true,
				component: () => import(/* webpackChunkName: "clientsView" */ '../views/DashboardView.vue'),
				meta: { 
					requiresAuth: true,
					middleware: role
				},
				children: [
					{
						path: ':userId/templates',
						name: 'clientsTemplates',
						props: true,
						component: () => import(/* webpackChunkName: "usersTemplates" */ '../views/TemplateView.vue'),
						meta: { 
							requiresAuth: true,
							middleware: role
						},
						beforeEnter(to, from, next) {
							const myRole = store.getters.getMyRole;
							if(myRole === 'admin') {
								next();
							} else {
								next('/');
							}
						},
					},
					{
						path: ':userId/reports',
						name: 'clientsReports',
						props: true,
						component: () => import(/* webpackChunkName: "usersReports" */ '../views/ReportView.vue'),
						meta: { 
							requiresAuth: true,
							middleware: role
						},
						beforeEnter(to, from, next) {
							const myRole = store.getters.getMyRole;
							if(myRole === 'admin') {
								next();
							} else {
								next('/');
							}
						},
					}
				]
			},
			{
				path: 'templates',
				name: 'templates',
				component: () => import(/* webpackChunkName: "templatesList" */ '../views/DashboardView.vue'),
				meta: { 
					requiresAuth: true,
					middleware: role
				},
			},
			{
				path: 'reports',
				name: 'reports',
				component: () =>
					import(/* webpackChunkName: "templatesList" */ '../views/DashboardView.vue'),
				meta: { 
					requiresAuth: true,
					middleware: role
				},
			},
		]
	},
	{
		path: 'login',
		name: 'Login',
		component: () =>
			import(/* webpackChunkName: "login" */ '../views/LoginView.vue'),
	},
	{ 
		path: 'error',
		name: 'error',
		component: () => import('../views/ErrorView.vue'),
		props: true
	}
];


const router = new VueRouter({
	mode: 'history',
	routes,
});

router.beforeEach((to, from, next) => {
	if (to.meta.middleware) {
		const middleware = Array.isArray(to.meta.middleware)
			? to.meta.middleware
			: [to.meta.middleware];

		const context = {
			from,
			next,
			router,
			to,
		};

		return middleware[0]({ ...context });
	}
	
	return next();
});

router.beforeEach((to, from, next) => {
	const myRole = store.getters.getMyRole;
	if(to.name === 'clients') {
		if(myRole === 'admin') {
			store.commit('setActiveDashboard', 'clients');
			next();
		} else {
			return next('/templates');
		}
	}
	if(to.name === 'reports') store.commit('setActiveDashboard', 'reports');
	if(to.name === 'templates') {
		if(myRole === 'user') {
			store.commit('setActiveDashboard', 'templates');
			next();
		} else {
			next('clients');
		}
	}
	if(to.name === 'clientsTemplates' || to.name === 'clientsReports') {
		store.commit('setActiveDashboard', 'clients');
		store.commit('setActiveDashboardCategory', 'user');
	}
	if(from.name === 'clientsTemplates' || from.name === 'clientsReports') {
		store.commit('setActiveDashboardCategory', store.getters.getLastClickedDashboardCategory);
	}
	next();
});

export default router;
